.FunctionEX {
    width: 100%;
    max-width: 1024px;

    display: flex;
    flex-direction: column;
    align-items: center;

}


/*  exf -  */


.SampleFucPage {
    width: 100%;
    max-width: 900px;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.exf-section1 {
    width: 100%;

    padding: 7px 0px;
    box-sizing: border-box;
}

.exf-section1>div {
    width: 100%;

    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.exf-title {
    margin: 5px 0px;
    /* text-align: center; */
}

.exf-title h5 {
    font-size: clamp(1rem, 1vw + 1rem, 2rem);
}

.exf-info p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}

.exf-return {
    cursor: pointer;
    user-select: none;
}

.exf-return p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
    color: blue;
    margin: 2.5px 0px;
}


.exf-section2 {
    width: 100%;

    padding: 2.5px 0px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
}


.exf-overview,
.exf-used {
    width: 100%;
    max-width: 900px;
    padding: 2px 0px;
    box-sizing: border-box;
    text-align: start;
}

.exf-overview>p,
.exf-used>p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
    padding: 2px 0px;
    box-sizing: border-box;
    margin-bottom: 5px;
}


.exf-section3 {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.exf-step {
    width: 100%;
    max-width: 900px;
}

.exf-step-title {
    width: 100%;
    padding: 5px 0px;
}

.exf-step-title>h6 {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}

.exf-step-preinfo {
    width: 100%;
    padding: 5px 0px;
}

.exf-step-preinfo>p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}

.exf-step-code {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}

.exf-step-gif {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: gray;
    padding: 10px;
    box-sizing: border-box;
}

.exf-step-gif img {
    width: 100%;
    height: auto;
}


.exf-section4 {
    width: 100%;

    padding: 10px 0px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
}


.exf-review {
    width: 100%;
    max-width: 900px;
    padding: 10px 0px;
    box-sizing: border-box;
}

.exf-review>h6 {
    font-size: clamp(0.9rem, 0.2vw + 0.9rem, 1rem);
    margin-bottom: 4px;
}

.exf-review>p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}