.ProgressPage {
    width: 100%;
    max-width: 1024px;
    margin-bottom: 30px;


    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}


.progress-head {
    width: 100%;
    padding: 10px 0px 30px 0px;
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

.progress-head>div {
    display: flex;
    justify-self: center;
    align-items: center;
    padding: 5px 0px;
}

.progress-info p {
    font-size: 0.8rem;
    font-weight: 600;
}

.progress-line {
    flex: 1 1;
    margin: 0px 20px;
}

.progress-line p {
    width: 100%;
    border-top: 1px solid;
}



.progress-body {
    width: 100%;
    min-height: 400px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
}

.progress-body>div {
    flex: 1 1;

    margin: 10px 0px;
    padding: 5px 0px;

    display: flex;
    flex-direction: row;
    justify-content: space-around;

    flex-wrap: wrap;
}



.progress-box-section1 {
    flex: 1 1;

}

.progress-box-section2 {
    flex: 1 1;

    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}

@media (max-width: 850px) {
    .progress-box-section1 {
        flex: 1 1 95%;
    }

    .progress-box-section2 {
        flex: 1 1 95%;
    }

}

.progress-box-title {
    margin: 2px 0px;
}

.progress-box-title>h5 {
    font-size: clamp(1rem, 1vw + 1rem, 2.5rem);
    font-weight: 700;
}

.progress-box-content {
    box-sizing: border-box;
}

.progress-box-content>p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
    padding: 5px 0px;
}

.progress-box-section2 ul {
    width: 100%;
    padding: 10px;
}

.progress-box-section2 li {
    list-style: none;
}

.progress-box-section2 li p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}