.AboutPage {
    width: 100%;
    max-width: 800px;
}

.about-section1 {
    width: 100%;
    margin-bottom: 20px;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.setction1-left {
    flex: 2 0;
    box-sizing: border-box;
    padding: 10px;

    display: flex;
    flex-direction: column;
}

.setction1-right {
    flex: 1 0;

    box-sizing: border-box;
    padding: 10px;

    display: flex;
    flex-direction: column;
}

.setction1-right p{
    color: blue;
    user-select: none;
    cursor: pointer;
}


.setction1-left-top {
    width: 100%;
    padding: 10px 0px;
    margin-bottom: 5px;
}

.setction1-left-top h4 {
    font-size: clamp(1rem, 1vw + 1rem, 2rem);
}

.setction1-left-middle {
    width: 100%;
}

.setction1-left-middle p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
    padding: 10px 0px;
}

.setction1-right-top {
    flex: 1 1;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}

.section1-sns {
    padding: 5px 0px;
}

.section1-sns p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}

@media (max-width: 850px) {

    .setction1-left {
        flex: 1 1 95%;
    }

    .setction1-right {
        flex: 1 1 95%;
        flex-direction: row;
    }

    .setction1-right-top {
        flex: 1 1;

        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }
}

.about-section2 {
    width: 100%;
    display: flex;
    flex-direction: column;
}


.about-skill-wrap {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}


.about-skill-box {
    width: clamp(20px, 2vw + 20px, 50px);
    aspect-ratio: 1/1;
}



/* line */


.about-line {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0px;
}

.about-line>.about-line-line {
    flex: 1;
    border-top: 1px solid;
}

.about-line>.about-line-text {
    margin: 0px 20px;
}

.about-line>.about-line-text>p {
    font-size: 0.8rem;
}



/* skill-hover */


.skill-html path {
    fill: #E34F26;
}

.skill-css path {
    fill: #1572B6
}

.skill-js path {
    fill: #F7DF1E
}

.skill-jq path {
    fill: #0769AD
}

.skill-react path {
    fill: #61DAFB
}

.skill-node path {
    fill: #5FA04E
}

.skill-express path {
    fill: #000000
}

.skill-mongo path {
    fill: #47A248
}