.NavPage {
    width: 100%;
    max-width: 1200px;
    height: 50px;

    display: flex;
    justify-content: center;
    align-items: center;

    margin-bottom: 70px;
}

.nav-wrap {
    width: 95%;
    max-width: 900px;
    /* height: 40px; */
    height: clamp(30px, 2vw + 15px, 40px);
    border-radius: 30px;
    padding: 1% 2%;
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

    position: relative;
}

.nav-manu {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;
}

.nav-text {
    padding: 5px 15%;
    cursor: pointer;
    user-select: none;
}

.nav-text>p {
    font-size: clamp(0.5rem, 1.5vw + 0.2rem, 0.8rem);
    font-weight: 500;
    text-shadow:
        -1px -1px 0 #ffffff,
        1px -1px 0 #ffffff,
        -1px 1px 0 #ffffff,
        1px 1px 0 #ffffff;
}

.nav-text:hover p {
    font-weight: bold;
}


.nav-background {
    position: absolute;
    border-radius: 30px;
    background: rgb(219, 219, 219);
    transition: 0.4s all ease-in-out;
}