.project-ex {
    width: 100%;
    max-width: 900px;

    display: flex;
    flex-direction: column;
    align-items: center;
}


.ex-section1 {
    width: 100%;
    max-width: 900px;
    margin-bottom: 10px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.ex-title {
    padding: 10px 0px;
    box-sizing: border-box;
}

.ex-title h4 {
    font-size: clamp(1.2rem, 2vw + 1.2rem, 3rem);
}

.ex-text {
    width: 100%;
    padding: 7.5px 0px;
    box-sizing: border-box;
}

.ex-text p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}

/* --------------------------------- */
.ex-section2 {
    width: 100%;
    max-width: 900px;
    background: rgb(0, 0, 0);

    display: flex;
    justify-content: center;
    align-items: center;
}

.ex-section2 iframe {
    width: 100%;
    aspect-ratio: 12/7;
}

.ex-section2 img {
    width: 100%;
    height: auto;
}

/* --------------------------------- */

.ex-section3,
.ex-section4 {
    max-width: 900px;
    width: 100%;
    padding: 10px 0px;
}

.ex-section3>div,
.ex-section4>div {
    width: 100%;
    padding: 5px 0px;
    box-sizing: border-box;
}

.ex-review h6 {
    font-size: clamp(0.7rem, 0.2vw + 1rem, 1.2rem);
    margin: 5px 0px;
}


.ex-review p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.875rem);
    padding: 7.5px 0px;
}

.ex-link li,
.ex-overview li,
.ex-function li,
.ex-good li,
.ex-bad li {
    list-style: none;
    padding: 2px 0px;
    margin: 2px 0px;
}

.ex-link li h6,
.ex-overview li h6,
.ex-function li h6,
.ex-good li h6,
.ex-bad li h6 {
    font-size: clamp(0.7rem, 0.2vw + 1rem, 1.2rem);
    margin: 5px 0px;
}

.ex-link li p,
.ex-overview li p,
.ex-function li p,
.ex-good li p,
.ex-bad li p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.875rem);
}


.ex-link li p {
    user-select: none;
    cursor: pointer;
    color: blue;
}

.ex-function .link {
    user-select: none;
    cursor: pointer;
    color: blue;
    margin-left: 5px;
    font-size: 0.8rem;
}