* {
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  min-height: 100vh;
  /* background: #DDCECD; */

  background: #f3f3f3;
}

.Cotainer {
  max-width: 1300px;
  min-height: 100vh;
  padding: 20px 10px 50px 10px;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;

  background: #ffffff;
}

.Footer {
  width: 100%;
  min-height: 150px;
  background: #f3f3f3;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Footer p {
  font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}


/* 구글 폰트 */

.suse {
  font-family: "SUSE", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}

.ibm-plex-sans-kr-thin {
  font-family: "IBM Plex Sans KR", sans-serif;
  font-weight: 100;
  font-style: normal;
}

.ibm-plex-sans-kr-extralight {
  font-family: "IBM Plex Sans KR", sans-serif;
  font-weight: 200;
  font-style: normal;
}

.ibm-plex-sans-kr-light {
  font-family: "IBM Plex Sans KR", sans-serif;
  font-weight: 300;
  font-style: normal;
}

.ibm-plex-sans-kr-regular {
  font-family: "IBM Plex Sans KR", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.ibm-plex-sans-kr-medium {
  font-family: "IBM Plex Sans KR", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.ibm-plex-sans-kr-semibold {
  font-family: "IBM Plex Sans KR", sans-serif;
  font-weight: 600;
  font-style: normal;
}

.ibm-plex-sans-kr-bold {
  font-family: "IBM Plex Sans KR", sans-serif;
  font-weight: 700;
  font-style: normal;
}


/* 구글 아이콘 */

.material-symbols-outlined {
  font-variation-settings:
    'FILL' 0,
    'wght' 200,
    'GRAD' 0,
    'opsz' 24
}