.IntroPage {
    width: 100%;
    max-width: 900px;
    margin-bottom: 30px;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.IntroPage>div {
    flex: 1 0 400px;
}

@media (max-width: 850px) {
    .IntroPage>div {
        flex: 1 1 95%;
    }

}

.intro-left {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 2rem;
    box-sizing: border-box;
}

.intro-left-top {
    width: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: clamp(1rem, 2vw + 2rem, 6rem);
}

.intro-left-top>h5 {
    font-size: clamp(1.2rem, 2vw + 1.2rem, 3rem);
    line-height: clamp(1rem, 2vw + 1rem, 2.0rem);
    font-weight: 500;
    text-shadow:
    -1px -1px 0 #ffffff,
    1px -1px 0 #ffffff,
    -1px 1px 0 #ffffff,
    1px 1px 0 #ffffff;
}

.intro-left-middle p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.9rem);
}

.intro-right {
    background: white;
}