.ProjectPage {
    width: 100%;
    max-width: 1024px;
    margin-bottom: 30px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}


.project-head {
    width: 100%;
    padding: 10px 0px 30px 0px;
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

.project-head>div {
    display: flex;
    justify-self: center;
    align-items: center;
    padding: 5px 0px;
}

.project-info p {
    font-size: 0.8rem;
    font-weight: 600;
}

.project-line {
    flex: 1 1;
    margin: 0px 20px;
}

.project-line p {
    width: 100%;
    border-top: 1px solid;
}

.project-body {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.project-box {
    width: 100%;
    min-height: 10vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;

    border-radius: 15px;
    padding: 15px;
    margin: 10px 0px;
    box-sizing: border-box;

    background: rgb(236, 236, 236);
}

.project-box>div {
    padding: 5px;
    box-sizing: border-box;
}

.project-box-left {
    flex: 1 1;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;

    margin: 20px;
}

.project-box-title {
    width: 100%;
    padding: 10px 0px;
    margin-bottom: 5px;
    user-select: none;
}

.project-box-title h5 {
    font-size: clamp(1rem, 2vw + 0.5rem, 1.5rem);
    font-weight: 600;
}

.project-box-description {
    width: 100%;
    display: flex;
    flex-direction: column;
    user-select: none;
}

.project-box-description>span {
    margin: 2.5px 0px;
    padding: 2.5px 0px;
    box-sizing: border-box;
}

.project-box-description .abstract p {
    font-size: clamp(0.8rem, 2vw + 0.3rem, 1rem);
}

.project-box-description .content p {
    font-size: clamp(0.7rem, 1vw + 0.3rem, 0.875rem);
}

.project-box-button {
    width: 100%;
    padding: 10px 0px;
    margin: 10px 0px;
}

.project-box-button>button {
    width: 80%;
    max-width: 100px;
    padding: 5px;
    box-sizing: border-box;
    border: none;
    border-radius: 10px;

    cursor: pointer;
    user-select: none;

    background: white;
}

.project-box-right {
    flex: 2.5 1;
}

.project-box-photo {
    width: 100%;
    border-radius: 10px;
    aspect-ratio: 10/7;
    overflow: hidden;

}

.project-box-photo img {
    width: 100%;
    height: auto;
    border-radius: 10px;
}

.project-box-photo iframe {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}



@media (max-width: 850px) {

    .project-box {
        padding: 5px;
    }

    .project-box-left {
        width: 100%;
        flex: 1 1 auto;
        margin: 5px;
    }

    .project-box-title {
        padding: 7.5px 0px;
        margin-bottom: 5px;
    }

    .project-box-right {
        display: none;
    }
}