.ArchitecturePage {
    width: 100%;
    max-width: 1024px;
    margin-bottom: 30px;


    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}


.arch-head {
    width: 100%;
    padding: 10px 0px 30px 0px;
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

.arch-head>div {
    display: flex;
    justify-self: center;
    align-items: center;
    padding: 5px 0px;
}

.arch-info p {
    font-size: 0.8rem;
    font-weight: 600;
}

.arch-line {
    flex: 1 1;
    margin: 0px 20px;
}

.arch-line p {
    width: 100%;
    border-top: 1px solid;
}

.arch-body {
    width: 100%;
    padding: 5px 0px 30px 0px;
    box-sizing: border-box;
}

.arch-box-wrap {
    width: 100%;
    min-height: 50px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.arch-box {
    width: 100%;
    padding: 15px 15px 20px 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;

    background: rgb(247, 223, 253);

    cursor: pointer;
    user-select: none;

    transition: all 0.5s ease-in-out;
}

.arch-box:hover {
    background: rgb(224, 105, 253);
}

.arch-box-top {
    width: 100%;
}

.arch-box-title,
.arch-box-info {
    width: 100%;
    padding: 2.5px 0px;
}

.arch-box-title h6 {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 1rem);
}

.arch-box-info p {
    font-size: clamp(0.7rem, 0.2vw + 0.7rem, 0.8rem);
}